import { useEffect, useState } from 'react';
import axios from 'axios';
import Items from '../components/Items';
import CustomPagination from './../components/CustomPagination';
import Genres from './../components/Genres';

export default function Series() {
  const [content, setContent] = useState([]);
  const [loading, setLoading] = useState(false);
  const [page, setPage] = useState(1);
  const [totalPages, setTotalPages] = useState();
  const [genres, setGenres] = useState([]);
  const [selectedGenres, setSelectedGenres] = useState([]);

  const giveGenereCode = () => {
    if (selectedGenres.length < 1) return;

    return selectedGenres
      .map((g) => g.id)
      .reduce((acc, curr) => acc + ',' + curr);
  };

  const genreforURL = giveGenereCode(selectedGenres);
  useEffect(() => {
    const fetchTvSeries = async () => {
      setLoading(true);
      try {
        const { data } = await axios.get(`
        https://api.themoviedb.org/3/discover/tv?api_key=${process.env.REACT_APP_API}&language=en-US&sort_by=popularity.desc&include_adult=false&include_video=false&page=${page}&with_genres=${genreforURL}`);
        setContent(data.results);
        setTotalPages(data.total_pages);
      } catch (error) {
        console.log('Error fetching trending content:', error);
      }

      setLoading(false);
    };
    fetchTvSeries();
  }, [page, genreforURL]);

  return (
    <div>
      <span className="pageTitle">Tv Series</span>
      <Genres
        type="tv"
        setSelectedGenres={setSelectedGenres}
        selectedGenres={selectedGenres}
        genres={genres}
        setGenres={setGenres}
        setPage={setPage}
      />

      <Items items={content} loading={loading} />

      {totalPages > 1 && (
        <CustomPagination setPage={setPage} totalPages={totalPages} />
      )}
    </div>
  );
}
